import{values}from'@ckb-lumos/base';import{bytes}from'@ckb-lumos/codec';export function createScript(config,args){if(!config){return undefined;}return{codeHash:config.CODE_HASH,hashType:config.HASH_TYPE,args:bytes.hexify(args)};}export function addCellDep(txSkeleton,newCellDep){var cellDep=txSkeleton.get('cellDeps').find(function(cellDep){return cellDep.depType===newCellDep.depType&&new values.OutPointValue(cellDep.outPoint,{validate:false}).equals(new values.OutPointValue(newCellDep.outPoint,{validate:false}));});if(!cellDep){txSkeleton=txSkeleton.update('cellDeps',function(cellDeps){return cellDeps.push({outPoint:newCellDep.outPoint,depType:newCellDep.depType});});}return txSkeleton;}