import{scriptToAddress}from'@onekeyhq/core/src/chains/ckb/sdkCkb';import{decodeBalanceWithCell}from"./balance";export function convertTokenHistoryUtxos(cell,mineAddress,tokenInfo,config){return cell?.map(function(c){return{address:scriptToAddress(c.cellOutput.lock,{config}),balance:decodeBalanceWithCell(c,config).shiftedBy(-tokenInfo.decimals).toFixed(),balanceValue:decodeBalanceWithCell(c,config).toString(),symbol:tokenInfo.symbol,isMine:scriptToAddress(c.cellOutput.lock,{config})===mineAddress};});}