import{bytes,number}from'@ckb-lumos/codec';import BigNumber from'bignumber.js';export var DEFAULT_CONFIRM_BLOCK=24;export function decodeNaiveBalance(cell){return new BigNumber(cell.cellOutput.capacity,16);}export function decodeBalanceWithCell(cell,config){if(cell?.cellOutput?.type){if(cell?.cellOutput?.type?.codeHash===config.SCRIPTS.XUDT?.CODE_HASH){return new BigNumber(number.Uint128.unpack(bytes.bytify(cell.data).slice(0,16)).toString());}if(cell?.cellOutput?.type?.codeHash===config.SCRIPTS.SUDT?.CODE_HASH){return new BigNumber(number.Uint128LE.unpack(bytes.bytify(cell.data).slice(0,16)).toString());}}return decodeNaiveBalance(cell);}export function selectCellsByAddress(confirmCell,targetAmount){var collectedSum=new BigNumber(0);var collected=[];for(var cell of confirmCell){collectedSum=collectedSum.plus(new BigNumber(cell.cellOutput.capacity,16));collected.push(cell);if(collectedSum.isGreaterThanOrEqualTo(targetAmount))break;}return{collected,collectedSum};}